import { Directive, HostListener, Input, Renderer2 } from '@angular/core';
import { MdbColorPickerComponent } from '../components/mdb-color-picker/mdb-color-picker.component';

@Directive({ selector: '[mdbColorPickerToggle]' })
export class MdbColorPickerToggleDirective {
  @Input() mdbColorPickerToggle: MdbColorPickerComponent;
  @Input() transitionTime: number = 200;

  private _isPickerVisible: boolean = false;
  private _clickEventTarget: any = null;

  constructor(private renderer: Renderer2) {}

  @HostListener('click', ['$event']) onClick(event: any) {
    this._clickEventTarget = event.target;
    this.toggleColorPicker();
  }

  @HostListener('window:click', ['$event']) onWindowClick(event: any) {
    if (
      event.target !== this._clickEventTarget &&
      this._isPickerVisible &&
      !this._getClosestEl(event.target, 'mdb-color-picker')
    ) {
      this.toggleColorPicker();
    }
  }

  public toggleColorPicker() {
    const opacityStyle = this._isPickerVisible ? '0' : '1';
    const visibilityStyle = this._isPickerVisible ? 'hidden' : 'visible';
    this.renderer.setStyle(
      this.mdbColorPickerToggle.el.nativeElement,
      'transition',
      `opacity ${this.transitionTime}ms`
    );
    this.renderer.setStyle(
      this.mdbColorPickerToggle.el.nativeElement,
      'opacity',
      opacityStyle
    );
    this.renderer.setStyle(
      this.mdbColorPickerToggle.el.nativeElement,
      'visibility',
      'visible'
    );
    setTimeout(() => {
      this.renderer.setStyle(
        this.mdbColorPickerToggle.el.nativeElement,
        'visibility',
        visibilityStyle
      );
    }, this.transitionTime);
    this._isPickerVisible = !this._isPickerVisible;
  }

  private _getClosestEl(el: any, selector: string) {
    for (; el && el !== document; el = el.parentNode) {
      if (el.matches && el.matches(selector)) {
        return el;
      }
    }
    return null;
  }
}
