import { Coords } from './../interfaces/coords.interface';
import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class MdbColorPickerService {
  private _segmentedColor: string;
  private _segmentedColorChange: Subject<string> = new Subject<string>();

  private _alpha = '0.5';
  private _alphaChange: Subject<string> = new Subject<string>();

  private _coords: Coords;
  private _coordsChange: Subject<Coords> = new Subject<Coords>();

  private _rgbaColor: Array<string> | string;

  private _rgbaSegments: Array<string> = ['255', '255', '255', '0.5'];

  private _color: { rgbaColor: string, hexColor: string } = { rgbaColor: '', hexColor: '' };
  private _colorChange: Subject<{ rgbaColor: string, hexColor: string }> = new Subject<{ rgbaColor: string, hexColor: string }>();

  private _colorPalette: any[] = [];
  private _colorPaletteChange: Subject<Array<any>> = new Subject<Array<any>>();

  public setColorPalette(colorArray: Array<any>) {
    this._colorPalette = colorArray;
    this._colorPaletteChange.next(this.getColorPalette());
  }

  public addColorToPalette(color: any) {
    this._colorPalette.push(color);
    this._colorPaletteChange.next(this.getColorPalette());
  }

  public getColorPalette() {
    return this._colorPalette;
  }

  public removeColorFromPalette(index: number) {
    this._colorPalette.splice(index, 1);
  }

  public colorPaletteWasChanged(): Observable<Array<any>> {
    return this._colorPaletteChange;
  }

  private setColor(rgbaColor?: any, hexColor?: any) {
    this._color.rgbaColor = rgbaColor;
    this._color.hexColor = hexColor;
    this._colorChange.next(this.getColor());
  }

  public getColor() {
    return this._color;
  }

  public colorWasChanged(): Observable<{ rgbaColor: string, hexColor: string }> {
    return this._colorChange;
  }

  setSegmentedColor(value: string) {
    this._segmentedColor = value;
    this._segmentedColorChange.next(this.getSegmentedColor());

    const rgbaColor = this.convertToRGBA();
    const hexColor = this.rgbaToHex(this.getRgbaColor());
    this.setColor(rgbaColor, hexColor);
  }

  getSegmentedColor() {
    return this._segmentedColor;
  }

  colorSegmentedWasChanged(): Observable<string> {
    return this._segmentedColorChange;
  }

  public setAlpha(value: string) {
    this._alpha = value;
    this._alphaChange.next(this.getAlpha());

    const rgbaColor = this.convertToRGBA();
    const hexColor = this.rgbaToHex(this.getRgbaColor());
    this.setColor(rgbaColor, hexColor);
  }

  public getAlpha() {
    return this._alpha;
  }

  public alphaWasChanged(): Observable<string> {
    return this._alphaChange;
  }

  public setCoords(value: Coords) {
    this._coords = value;
    this._coordsChange.next(this.getCoords());
  }

  public getCoords() {
    return this._coords;
  }

  public coordsWasChanged(): Observable<Coords> {
    return this._coordsChange;
  }

  protected convertToRGBA() {
    this._rgbaColor = this.getSegmentedColor().split(',');
    const alphaChannel = this.getAlpha();
    this._rgbaSegments[0] = this._rgbaColor[0];
    this._rgbaSegments[1] = this._rgbaColor[1];
    this._rgbaSegments[2] = this._rgbaColor[2];
    this._rgbaSegments[3] = alphaChannel;
    return 'rgba(' + this._rgbaSegments.toString() + ')';
  }

  public getRgbaColor() {
    return this.convertToRGBA();
  }

  public getHexColor() {
    return this.rgbaToHex(this.getSegmentedColor() + ',' + this.getAlpha());
  }

  public rgbaToHex(value: any) {
    value = value.match(/^rgba?[\s+]?\([\s+]?(\d+)[\s+]?,[\s+]?(\d+)[\s+]?,[\s+]?(\d+)[\s+]?/i);
    return (value && value.length === 4) ? '#' +
      ('0' + parseInt(value[1], 10).toString(16)).slice(-2) +
      ('0' + parseInt(value[2], 10).toString(16)).slice(-2) +
      ('0' + parseInt(value[3], 10).toString(16)).slice(-2) : '';
  }

  public hexToRgba(value: string, alpha?: string) {
    const r = parseInt(value.slice(1, 3), 16);
    const g = parseInt(value.slice(3, 5), 16);
    const b = parseInt(value.slice(5, 7), 16);

    if (alpha) {
      return `rgba(${r}, ${g}, ${b}, ${alpha})`;
    } else {
      return `rgb(${r}, ${g}, ${b})`;
    }
  }

  constructor() { }
}
